/* src/AgathaPage.css */

.celebration {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
  }
  
  .celebration-text {
    font-size: 3em;
    color: #ff1493;
    animation: flash 1s infinite;
  }
  
  @keyframes flash {
    0% { opacity: 1; }
    50% { opacity: 0.5; }
    100% { opacity: 1; }
  }
  
  .confetti {
    position: absolute;
    width: 100%;
    height: 100%;
    background: url('https://cdn.pixabay.com/photo/2014/04/03/10/52/confetti-310302_960_720.png') no-repeat center;
    background-size: cover;
    z-index: 0;
    animation: fall 2s infinite;
  }
  
  @keyframes fall {
    0% { transform: translateY(-100%); }
    100% { transform: translateY(100%); }
  }
  